<template>
	<div class="login">
		<view class="loginContent">
			<view class="loginTop">
				<view class="loginImg">
					<image :src="registeredLogo" mode=""></image>
				</view>
				<view class="">
					<view class="loginContent0">欢迎使用律小购</view>
					<view class="loginContent1">法律人工智能服务平台 </view>
				</view>
			</view>
			<comp-input :password='false' :firstName="firstName" @myblur="myblur" placer="输入您的账号"></comp-input>
			<comp-input :password='true' :firstName="firstName1" @myblur="myblurpassword" placer="输入您的密码"></comp-input>

			<view class="loginButtom">
				<button type="primary" @click="login" :disabled="disabled">登录</button>
			</view>
		</view>

	</div>

</template>

<script>
	import {
		firstName,
		firstName1
	} from "@/static/images/lxg/base64.js";
	import {
		login
	} from "@/api/index.js";
	import w_md5 from "@/js_sdk/zww-md5/w_md5.js"
	import compInput from '@/components/compInput'

	// import md5 from 'crypto-js/md5'
	export default {
		components: {
			compInput
		},
		data() {
			return {
				firstName: firstName,
				firstName1: firstName1,
				disabled: false,
				account: '',
				md5Pwd: "",
				registeredLogo: require('../../static/images/lxg/registered-logo.png')
			};
		},
 
		methods: {
			myblur(messarr) {
				this.account = messarr[0]
				console.log("父组件接受到的值", messarr)
				 this.showButtom()
			},
			myblurpassword(messarr) {
				this.md5Pwd = messarr[0]
				console.log("父组件接受到的值", messarr)
				this.showButtom()
			},
			showButtom(){
				if(this.account != "" && this.md5Pwd !=""){
					this.disabled = false
				}else{
					this.disabled = true
				}
			},
		 	login() {
				// https://ext.dcloud.net.cn/plugin?id=4031
				let params = {
					account: this.account,
					md5Pwd: w_md5.hex_md5_32(this.md5Pwd),
					accountType: '2',
					equipmentType: 'app',
				}


				// https://uat-api.lvxiaogou.com/sso/user/login
				// https://dev-pub-mob.lvxiaogou.com/sso/user/login
			 	login(params).then(res => {
					console.log("获取数据成功",res)
					if(res.code==200){
						 
							let tokenData = JSON.stringify(res.data);
							
							 uni.setStorage({
								key: "storage_token",
								data: tokenData,
								success: function() {
									console.log("success");
								}
							});
							uni.navigateTo({
								url:"/pages/login/role"
							})
						 
						 
					}
					 

				})

			}
		},
		onLoad(options) {
			// let pass=this.$md5()
			// console.log(pass)
		}
	};
</script>

<style>
	.loginContent {
		padding: 20px
	}

	.loginTop {
		display: flex;
		flex-direction: row;
	}

	.loginButtom {
		margin-top: 40px
	}

	.loginButtom button {
		border-radius: 100upx;
	}

	.loginImg image {
		width: 2rem;
		height: 2.4rem;
		margin-right: .74667rem;
	}

	.loginContent0 {
		font-size: 38upx;
		color: #423831;
		font-weight: 700;
		margin-bottom: 20upx
	}

	.loginContent1 {
		font-size: 30upx;
		color: #a2a2a2;
	}
</style>
